import isMatch from 'lodash/isMatch'
import { useCallback, useEffect, useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory, useLocation } from 'react-router-dom'
import { RootState } from './../../store'
import { filterActions } from './Filter.actions'
import { Filter } from './Filter.interface'
import { mapFilterToQueryString, getEmptyFilter, mapQueryStringToFilter } from './Filter.utils'
import { usePencilCache } from '../Pencil/Pencil.hooks'
import { useCountryRecord } from '../Taxonomy/Taxonomy.hooks'

export const useFilter = () => {
  const filter = useSelector<RootState, Filter>(store => store.filter)
  const dispatch = useDispatch()

  const dispatchFilter = useCallback(
    (newFilter: Filter) => {
      if (!isMatch(filter, newFilter)) {
        dispatch(filterActions.set(newFilter))
      }
    },
    [filter, dispatch],
  )

  const updateFilter = useCallback(
    (update: Partial<Filter>) => dispatchFilter({ ...filter, ...update }),
    [dispatchFilter, filter],
  )

  const setFilter = useCallback(
    (update: Partial<Filter>) => dispatchFilter({ ...getEmptyFilter(), ...update }),
    [dispatchFilter],
  )

  const isFiltered = Boolean(filter.country || filter.tag)

  return [
    filter,
    {
      isFiltered,
      setFilter,
      updateFilter,
    },
  ] as const
}

export const useFilerQueryString = () => {
  const history = useHistory()
  const { search } = useLocation()
  const [filter, { updateFilter }] = useFilter()
  const skipAction = 'POP'
  const actionRef = useRef('')

  useEffect(() => {
    const newSearch = mapFilterToQueryString(filter)
    if (newSearch !== search && actionRef.current !== skipAction) {
      history.push({ search: newSearch, state: filter })
    }
  }, [filter, history, search])

  useEffect(() => {
    const unlisten = history.listen(({ search, state }, action) => {
      actionRef.current = action
      if (action === skipAction) {
        updateFilter(mapQueryStringToFilter(search))
      }
    })

    return () => {
      unlisten()
    }
  }, [history, updateFilter])
}

export const useFilterCountries = () => {
  const cache = usePencilCache()
  const countries = useCountryRecord()

  return (cache?.geo || []).map(geoId => countries[geoId])
}
